<template>
	<div class="saleedi-add-address">
		<van-cell-group class="address-form fx-edi-block">
			<van-field v-model="consignee" label="" placeholder="" />
			<van-field v-model="telePhone" label="" placeholder="" />
			<van-field v-model="areaString" :readonly="true" label="" placeholder="//" @click="areaClick"/>
			<van-field
				v-model="address"
				type="textarea"
				maxlength="50"
				rows="2"
				label=""
				placeholder="" />
			<van-switch-cell v-model="defaultFlag" title="" />
		</van-cell-group>
		<div v-if="!editAddressFlag" class="btn-baox">
			<van-button class="confirm-btn" type="primary" round @click="addClick"></van-button>
			<van-button class="cancel-btn" round @click="cancelClick"></van-button>
		</div>
		<div v-if="editAddressFlag" class="btn-baox">
			<van-button class="confirm-btn" type="primary" round @click="editClick"></van-button>
			<van-button class="cancel-btn" type="danger" round @click="deleteClick"></van-button>
		</div>
		<van-popup v-model="areaShow" position="bottom">
			<van-area v-model="areaCode" :area-list="areaData" @confirm="areaConfirmhandle" />
		</van-popup>
	</div>
</template>

<script>
import areaJson from '@/js/lib/area.js'
export default {
	name: 'addressList',
	data () {
		return {
			$query: this.$route.query,
			editAddressFlag: false,
			addressId: '',
			areaData: areaJson,
			areaCode: '',
			areaObj: [],
			areaString: '',
			address: '',
			consignee: '',
			telePhone: '',
			defaultFlag: false,
			areaShow: false
		}
	},
	created () {
		if (this.$route.query.edit) {
			this.initEditAddress(this.$route.query)
		}
	},
	watch: {
		areaObj (val) {
			if (val.length > 0) {
				let areaArr = val.map(item => {
					return item.name
				})
				this.areaString = areaArr.join('-')
				this.areaCode = val[2].code
			} else {
				this.areaString = ''
				this.areaCode = ''
			}
		}
	},
	methods: {
		initEditAddress (query) {
			this.editAddressFlag = true
			this.addressId = query.id
			this.consignee = query.consignee
			this.telePhone = query.telePhone
			this.address = query.address
			this.areaString = query.region
			this.defaultFlag = query.defaultFlag === '1'
		},
		areaClick () {
			this.areaShow = true
		},
		areaConfirmhandle (area) {
			this.areaShow = false
			this.areaObj = area
		},
		addClick () {
			const params = {
				consignee: this.consignee,
				telePhone: this.telePhone,
				address: this.address,
				region: this.areaString,
				defaultFlag: this.defaultFlag ? '1' : '0'
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/addReceipt`,
				data: params
			}).then(res => {
				debugger
				console.log(this)
				this.$fxGo('BACK', this.$router)
			})
		},
		cancelClick () {
			this.$fxGo('BACK', this.$router)
		},
		editClick () {
			const params = {
				id: this.addressId,
				consignee: this.consignee,
				telePhone: this.telePhone,
				address: this.address,
				region: this.areaString,
				defaultFlag: this.defaultFlag ? '1' : '0'
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/editReceipt`,
				data: params
			}).then(res => {
				this.$fxGo('BACK', this.$router)
			})
		},
		deleteClick () {
			const params = {
				id: this.addressId
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/deleteReceipt`,
				data: params
			}).then(res => {
				this.$fxGo('BACK', this.$router)
			})
		}
	}
}
</script>
<style lang="stylus" scoped>
.saleedi-add-address {
	background:#fff
	height:100%
	.address-form {
		width:320px
		margin:10px auto
		text-align:left
	}
	.btn-baox {
		width:100%
	}
	.confirm-btn,.cancel-btn {
		width:320px
		margin-top:20px
	}
}
</style>
